// Font Path
$font-path: "../fonts" !default;

// Open Sans
$open-sans-font-path: $font-path + "/open-sans";

@font-face {
    font-family: 'Open Sans';
    font-weight: 300;
    font-style: normal;
    src: local('Open Sans Light'),
        url("#{$open-sans-font-path}/OpenSans-Light.woff2") format("woff2"),
        url("#{$open-sans-font-path}/OpenSans-Light.woff") format("woff"),
        url("#{$open-sans-font-path}/OpenSans-Light.ttf") format("truetype");
}

@font-face {
    font-family: 'Open Sans';
    font-weight: 300;
    font-style: italic;
    src: local('Open Sans Light Italic'),
        url("#{$open-sans-font-path}/OpenSans-LightItalic.woff2") format("woff2"),
        url("#{$open-sans-font-path}/OpenSans-LightItalic.woff") format("woff"),
        url("#{$open-sans-font-path}/OpenSans-LightItalic.ttf") format("truetype");

}

